        <div>
          <el-select v-model="selectedAuthor"
                     placeholder="请选择"
                     style="width: 220px;">
            <el-option v-for="item in authorList"
                       :key="item.id"
                       :label="item.name"
                       :value="item.id">
            </el-option>
          </el-select>
        </div>
        <div class='addAuthor'
             @click="hidden">
          新增/删除作耆
        </div>
      </div>
      <div class="color">
        <div class="text">
          模式
        </div>
        <div style="height: 40px; line-height: 40px;">

          <el-radio-group v-model="mode"
                          @change="editMode">
            <el-radio v-model="mode"
                      label='true'
                      input=editMode>3+2</el-radio>
            <el-radio v-model="mode"
                      label='false'>2+2</el-radio>
          </el-radio-group>
        </div>
      </div>
      <div class="isSearching">
        <div class="text">都在搜</div>
        <div>
          <el-input v-model="isSearching"
                    placeholder="请输入显示的大家都在搜内容"
