        <div>
          <el-input v-model="reviews"
                    type="number"
                    placeholder="请输入评论数"
                    style="width: 220px;"></el-input>
        </div>
      </div>
    </div>
    <div class="authorMode"
         v-if="mode=='true'?true:false">
      <div class="listValue">
        <div class="content">
          <div class="contentText">用户发言</div>
          <el-input v-model="authorMode.user1"
                    placeholder="请输入内容"
                    type="textarea"
                    class="contentValue"></el-input>
          <el-input type="number"
                    autosize
                    placeholder="点赞数"
                    class="praisesNumber"
                    v-model="authorMode.user1Praise">
          </el-input>
        </div>
        <div class="content">
          <div class="contentText"
               style="color:#ff9854">作者发言</div>
          <el-input v-model="authorMode.author"
                    placeholder="请输入内容"
                    type="textarea"
                    class="contentValue"></el-input>

          <el-input type="number"
                    autosize
                    placeholder="点赞数"
                    class="praisesNumber"
                    v-model="authorMode.authorPraise">
          </el-input>
        </div>
        <div class="content">
          <div class="contentText">用户发言</div>
          <el-input v-model="authorMode.user2"
                    placeholder="请输入内容"
                    type="textarea"
                    class="contentValue"></el-input>
          <el-input type="number"
                    autosize
                    placeholder="点赞数"
                    class="praisesNumber"
                    v-model="authorMode.user2Praise">
          </el-input>
        </div>
      </div>
    </div>
    <div class="list"
         v-for="item,index in dialogueList"
         :key="item.user">
      <div class="listValue">
        <div class="content">
          <div class="contentText">用户发言</div>
          <el-input v-model="item.user"
                    placeholder="请输入内容"
                    type="textarea"
                    class="contentValue"></el-input>
          <el-input type="number"
                    autosize
                    placeholder="点赞数"
                    class="praisesNumber"
                    v-model="item.userPraise">
          </el-input>
        </div>
        <div class="content">
          <div class="contentText"
               style="color:#ff9854">作者发言</div>
          <el-input v-model="item.author"
                    placeholder="请输入内容"
                    type="textarea"
                    class="contentValue"></el-input>

          <el-input type="number"
                    autosize
                    placeholder="点赞数"
                    class="praisesNumber"
                    v-model="item.authorPraise">
          </el-input>
        </div>
      </div>
      <div class="add">
        <i class="el-icon-circle-plus-outline"
           @click="addList"></i>
      </div>
      <div class="add">
        <i class="el-icon-remove-outline"
           @click="delList(index)"></i>
      </div>
    </div>
    <div>
      <el-dialog title="编辑作者"
                 :visible.sync="isHidden"
                 width="70%"
                 :before-close="hidden"
                 class="addAuthor">
        <el-button type="primary"
                   size="small"
                   round
                   @click="isAddAuthor=true">新增作者</el-button>
        <div>
          <div>
            <el-table :data="authorList"
                      style="width: 100%">
              <el-table-column label="头像"
                               width=300>
                <template #default="{ row }">
                  <img :src="row.img"
                       alt="已存储的图片"
                       style="max-width: 100px;" />
                </template>

              </el-table-column>
              <el-table-column prop="name"
                               label="姓名"
                               width=300>
              </el-table-column>
              <el-table-column label="操作">
                <template #default="{ row }">
                  <i class="el-icon-delete"
                     @click="deleteData(row.id)"></i>
                </template>

              </el-table-column>
            </el-table>
          </div>
        </div>

        <span slot="footer"
              class="dialog-footer">
          <el-button @click="isHidden = false">取 消</el-button>
          <el-button type="primary"
                     @click="isHidden = false">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog title="新增作者"
                 :visible.sync="isAddAuthor"
                 width="50%"
                 class="addAuthor">
        <div>
          <div style="display: flex; height: 100px;display: flex;
  align-items: center;">
            <div style="margin-right: 30px;">头像</div>
            <!-- 上传按钮 -->
            <input type="file"
                   accept="image/*"
                   @change="handleFileUpload"
                   v-if="!AddAuthor.img" />
            <!-- 显示已上传的图片 -->
            <div v-if="AddAuthor.img"
                 style="margin-right: 30px;display: flex;
  align-items: center; ">
              <img :src="AddAuthor.img"
                   alt="已存储的图片"
                   style="max-width: 100px;margin-right: 20px;" />
              <button @click="deleteImage">删除图片</button>
            </div>
          </div>
          <div style="display: flex;">
            <div style="line-height: 40px;">
              作者名称：
            </div>
            <div>
              <el-input v-model="AddAuthor.name"
                        placeholder="请输入内容"></el-input>
            </div>
          </div>
        </div>

        <span slot="footer"
              class="dialog-footer">
          <el-button @click="isAddAuthor = false">取 消</el-button>
          <el-button type="primary"
                     @click="addAuthor">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>
  
  <script lang="ts">
import Vue from 'vue'
import axios from 'axios'
import IndexedDB from './indexedDB'
export default Vue.extend({
  name: 'HomeA',
  data() {
    return {
      db: null as any,
      message: '' as any,
      isHidden: false,
      isAddAuthor: false,
      selectedAuthor: 1,
      isSearching: '',
      selectedColor: 'white',
      mode: 'true',
      maxSize: 1 * 1024 * 1024, // 限制图片大小为 1MB
      authorMode: {
        user1: '',
        user1Praise: 0,
        author: '',
        authorPraise: 0,
        user2: '',
        user2Praise: 0,
      },
      reviews: 0,
      AddAuthor: {
        id: 0,
        img: '' as any,
        name: '' as any,
      },
      authorList: [],
      dialogueList: [
        {
          user: '',
          userPraise: 0,
          author: '',
          authorPraise: 0,
        },
      ],
    }
  },
  async created() {
    this.db = new IndexedDB('MyDatabase', 'MyStore')
    await this.db.openDB()
    this.getData()
  },
  //   mounted() {},
  methods: {
    hidden() {
      this.isHidden = !this.isHidden
    },
    addList() {
      this.dialogueList.push({
        user: '',
        userPraise: 0,
        author: '',
        authorPraise: 0,
      })
    },
    delList(index: number) {
      this.dialogueList.splice(index, 1)
    },
    editMode() {
      this.authorMode = {
        user1: '',
        user1Praise: 0,
        author: '',
        authorPraise: 0,
        user2: '',
        user2Praise: 0,
      }
    },
    // 处理文件选择
    handleFileUpload(event: any) {
      const file = event.target.files[0]
      if (!file) return
      // 验证文件类型
      if (!file.type.startsWith('image/')) {
        alert('请选择图片文件！')
        return
      }
      if (file.size > this.maxSize) {
        alert('图片大小不能超过 1MB')
        return
      }
      // 将图片转为 Base64
      const reader = new FileReader()
      reader.onload = (e) => {
        this.AddAuthor.img = e.target == null ? '' : e.target.result
      }
      reader.readAsDataURL(file)
    },
    // 删除图片
    deleteImage() {
      this.AddAuthor.img = ''
      alert('图片已删除！')
    },
    async addAuthor() {
      if (!this.AddAuthor.img) {
        alert('请上传头像！')
      } else if (!this.AddAuthor.name) {
        alert('请填写用户名')
      } else {
        this.AddAuthor.id = Math.floor(Math.random() * 100)
        try {
          this.message = await this.db.addItem(this.AddAuthor)
          this.getData()
        } catch (error) {
          this.message = error
        }
        this.isAddAuthor = false
      }
    },
    cancelAddAuthor() {
      this.AddAuthor.img = ''
      this.AddAuthor.name = ''
      this.isAddAuthor = false
    },
    async getData() {
      try {
        this.authorList = await this.db.getAllItems()
        this.message = 'Data retrieved'
      } catch (error) {
        this.message = error
      }
    },
    async deleteData(id: any) {
      try {
        this.message = await this.db.deleteItem(id)
        this.getData()
      } catch (error) {
        this.message = error
      }
    },
  },
})
</script>
